import React, { Component } from 'react';

//My components
import Navbar from './components/Navbar'
import Chart from './components/Chart'
import Table from './components/Table'

import swal from 'sweetalert'

class App extends Component {

    constructor(props){
      super(props)
      this.state = {
        points: []
      }
      this._onClick = this._onClick.bind(this)
      this.handler = this.handler.bind(this)
    }

    componentDidMount(){
      if(localStorage.getItem('points')){
        const points = JSON.parse(localStorage.getItem('points'))
        this.setState({
          points
        })
      }
    }

    handler = (ChildToParent) =>{
      this.setState({points: ChildToParent})
      localStorage.setItem('points', JSON.stringify(ChildToParent))
    }

    _onClick(){
      let date = "0"
      swal('¡Ingresa la fecha!',{
        content: {
           element: "input",
           attributes: {
             type: "date",
           },
         },

        buttons: {
          cancel: false,
          confirm: true,
        },
      }).then((value) =>{
        date = value
        if(date === '' || date === null){
          swal('','Introduce una fecha.', 'info')
        }
        else{
        swal('¡Ingresa tu peso!',{
          content: {
             element: "input",
             attributes: {
               type: "number",
             },
           },

          buttons: {
            confirm: true,
          },
        }).then((value) => {
          value = Number(value)
          if(value < 1 || value > 200 || isNaN(value)){
            swal('','Introduce un valor correcto.', 'info')
          }
          else{
            this.addPoint(value, date)
          }
        })
      }
    })


    }

    addPoint(point, date){
      const newRegister = [
        date,
        point
      ]

      const points = [...this.state.points, newRegister]

      this.setState({
        points
      })

      localStorage.setItem('points', JSON.stringify(points))
    }

  render() {
    return (
      <div>
        <Navbar />
        <div className="row" style={{paddingTop: 20}}>
          <div className="col l6 m6 s12"><Chart handler={this.handler} points={this.state.points} /></div>
          <div className="col l6 m6 s12"><Table points={this.state.points} /></div>
        </div>
        <a
          style={{opacity: 0.85}}
          className="#00bfa5 teal accent-4 btn-floating btn-large fixed-action-btn"
          onClick={this._onClick}>
           <i className="material-icons">add</i>
        </a>
      </div>
    );
  }
}

export default App;



